body{
  background:url('/im.png');
}
.App{
  background-color: #c7d3d800;
  min-height:500px;
  width: 600px;
  margin: 40px auto;
}
.header {
  color: brown;
  font-size:70px; 
  font-style: italic;
  margin-bottom: 18px;
  padding:18px 0;
  margin-left: 120px;
 
}

#to-do-form input{
  background-color: rgb(19, 96, 102);
  border: 0; 
  color:black;
  font-style: italic;
  width: 250px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
  margin-left: 100px;
}
#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:rgba(202, 156, 24, 1); 
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;
}